<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				open: false,
				user_info: {},
				balance: '1',
				paytype: '1',
				vip_price: '',
				data: [],
				vip_list: [],
				vip_item: {},
				pata: {},
				openSimple: false
			}
		},
		methods: {
			closeBottomSheet() {
				this.open = true;
			},
			openBotttomSheet(item) {
				// 接收用户点击的 数据 赋值给 vip_item
				this.vip_item = item;
				this.open = true;
			},
			openSimpleDialog (item) {
				this.vip_item = item;
		      this.openSimple = true;
		    },
		    closeSimpleDialog () {
		      this.openSimple = false;
		      this.userInfo()
		    },
			back() {
				window.history.back();
			},
			//			获取会员级别
			vips() {
				var that = this;
				var params = {

				};
				that.$http.post('/user/vips', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						// 定义变量vip_list 用来接收 会员数据列表  然后去页面 循环展示
						that.vip_list = res.data;
					}
				})
			},
			//			充值会员
			charge_money(e) {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					balance: that.vip_item.vip_price, //这里就是 用户点击的那一条数据
					paytype: e,
					level: that.vip_item.vip_id
				};
				that.$http.post('/user/charge_money', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						if( e==2 ){
//							console.log( res )
//							console.log("2")
							dsBridge.call("zfb_pay",res.data);      
							dsBridge.register("alipayMsg", function(r) {
								if(r=='1'){
									that.closeSimpleDialog();
									that.userInfo();
								}else{
									
								}
							})
						}else{
//							console.log( res )
//							console.log("1")
							dsBridge.call("wx_pay",JSON.stringify( res.data ));      
							dsBridge.register("wxpayMsg", function(r) {
								      
							})
						}
//						this.closeSimpleDialog();
						
					}
				})
			},
			//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.pata = res.data;
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.vips();
			this.userInfo();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.upgrade-container {
